
export default {
	
	onLaunch() {
		this.getLocalProvider();
	},
	onShow() {
		console.log('App Show');
	},
	onHide() {
		console.log('App Hide');
	},
	methods: {
		getLocalProvider() {
			let providerList =[]
			uni.getProvider({
				service: 'oauth',
				success: result => {
					providerList = result.provider.map(value => {
						let providerName = '';
						let img = ''
						switch (value) {
							case 'weixin':
								providerName = '微信登录';
								img='http://admin.0752hb.com/baike/weixin.png'
								break;
							case 'qq':
								providerName = 'QQ登录';
								img = 'http://admin.0752hb.com/baike/qqhy.png';
								
								break;
							case 'sinaweibo':
								providerName = '新浪微博登录';
								img = 'http://admin.0752hb.com/baike/weibo.png';
								break;
							case 'xiaomi':
								providerName = '小米登录';
								img = 'http://admin.0752hb.com/baike/xiaomi.png';
								break;
							case 'alipay':
								providerName = '支付宝登录';
								img = 'http://admin.0752hb.com/baike/zhifubao.png';
								break;
							case 'baidu':
								providerName = '百度登录';
								img = 'http://admin.0752hb.com/baike/baidu.png';
								break;
							
						}
						return {
							name: providerName,
							img:img,
							id: value
						};
					});
					this.$store.commit('setProviderList',providerList)
				},
				fail: error => {
					console.log('获取登录通道失败', error);
				}
			});
		}
	}
};
